<template>
  <div id="loginPage">
    <!--<img class="bg_img" src="../../assets/images/bg.png" alt="">-->
    <div class="header">
      <div>尊敬的用户，欢迎使用 <span>小月智慧环卫云平台</span></div>
      <div style="padding-right: 43px"><img src="../../assets/images/phone.png" alt=""></div>
    </div>
    <div class="content">
      <div class="loginBox">
        <input v-model="form.Name" class="input-login" maxlength=20 type="text">
        <input @keyup.enter="login()" maxlength=20  v-model="form.Pass" class="input-login input-login2" type="password">
        <img class="img1" src="../../assets/images/touxiang.png" alt="">
        <img class="img2" src="../../assets/images/mima.png" alt="">
        <div class="passtext flex">
          <div class="flex">
            <!--<input type="checkbox" style="width: 20px;height: 20px;margin-top: 6px"> 记住密码-->
            <el-checkbox style="margin-top: 6px;color: #02576C;" v-model="checked">记住密码</el-checkbox>
          </div>
           <div style="cursor:pointer;">找回密码</div>
        </div>
        <div class="longinBtn">
          <el-button @click="login()" type="primary">登录</el-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import {mixin} from '../../untils/mixin'

  export default {
    name: 'login',
    data() {
      return {
        form: {
          Name: '',
          Pass: ''
        },
        response: '',
        testUrl: '',
        date1: '',
        checked: false
      }
    },
    mixins: [mixin],
    created() {
      this.init()
    },
    methods: {
      init() {
        let checked = localStorage.getItem('checked')
        if (checked) {
          this.checked = true
          this.form = JSON.parse(checked)
        }
      },
      login() {
        if (!this.form.Name || !this.form.Pass) {
          this.$message.warning('请输入用户名或密码')
          return
        }
        this.$api.login(this.form).then((res) => {
        //  console.log(res)
          this.response = JSON.stringify(res)
          if (res.State === 0) {
            this.$message.success('登录成功')
            this.$router.push({path: '/main', query: {id: 1}})
            if (this.checked) {
              localStorage.setItem('checked', JSON.stringify(this.form))
            } else {
              localStorage.setItem('checked', '')
            }
          }
          if (res.State === 1000) {
            this.$message.error('用户名或密码错误')
          }
        }, errorMessage => {
          console.log(errorMessage)
        })
      },
      testGet() {
        if (!this.testUrl) {
          this.$message.warning('请输入测试get路径')
          return
        }
        this.$api.testGet(this.testUrl).then((res) => {
          this.response = JSON.stringify(res)
          if (res) {
            this.$message.success('测试接口调用成功')
          }
        })
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
  @import (reference) "../../style/mixin";
  #loginPage {
    .flex();
    flex-direction: column;
    height: 100%;
    width: 100%;
    //.commonBack('../../assets/images/bg.png');
    background-size: cover;
    -webkit-background-size: cover;
    .header{
      width: 100%;
      height: 78px;
      background-color: #272b3f;
      color: #fff;
      padding-left: 81px;
      .font(18px, #fff);
      .flex(@justify-content: space-between);
      span{
        font-weight: bold;
        .font(19px, #fbd420)
      }
    }
    .content{
      flex: 1;
      width: 100%;
      .commonBack('../../assets/images/bg.jpg');
      background-size: cover;
      .passtext{
        justify-content: space-between;
        .font(14px, #02576C);
        padding: 6px 10px;
      }
      .longinBtn{
        width: 300px;
        height: 40px;
        margin-top: 4px;
        .el-button{
          height: 100%;
       //   line-height: 40px;
        //  background-color: #1186fd;
          border-radius: 20px;
          width: 100%;
          font-size: 17px;
          color: #fff;
          letter-spacing: 17px;
          text-align: center;
          font-weight: 500;
          border: 0;
        }
      }
    }
    .loginBox{
      width: 367px;
      height: 273px;
      border-radius: 9px;
     // border: 1px solid #ccc;
     // box-shadow: 0px 0px 3px 3px #ddd;
      .positionCenter;
      .commonBack('../../assets/images/bg-login.png');
      top: 40%;
      z-index: 1000;
      padding: 33px;
     // background-color: #caecfe;
      .input-login{
        width: 300px;
        height: 45px;
        border-radius: 5px;
        border: 0;
       // box-shadow: 0px 0px 3px 3px #ccc;
        font-size: 15px;
        background-image: url("../../assets/images/name-input.png");
        padding-left: 43px;
      }
      .input-login2{
        margin-top: 26px;
      }
      .img1{
        position: absolute;
        top: 45px;
        left: 45px;
      }
      .img2{
        position: absolute;
        top: 113px;
        left: 45px;
      }
    }
    .bg_img{
      position: fixed;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      margin: auto;
      z-index: 0;
    }
    .test {
      margin: 0 auto;
      padding: 20px 0;
      width: 500px;
      display: flex;
      align-items: center;
      .div {
        width: 130px;
        .font(30px, @text-align: center)
      }
    }
    h1, h2 {
      font-weight: normal
    }
    ul {
      list-style-type: none;
      padding: 0
    }
    li {
      display: inline-block;
      margin: 0 10px
    }
    a {
      color: #42b983
    }
  }
</style>



